html,
body {
  color: #444;
  font-size: 16px;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: 400;
  margin: 0px;
  padding: 0px;
}
.padding-0{
  padding: 0px;
}
.padding-top-5 {
  padding-top: 5px;
}
.padding-top-12 {
  padding-top: 12px;
}
.padding-top-120 {
  padding-top: 120px;
}
.padding-top-100 {
  padding-top: 100px;
}
.padding-right-16 {
  padding-right: 16px;
}
.padding-left-16 {
  padding-left: 16px;
}
.padding-bottom-20 {
  padding-bottom: 20px;
}
.padding-bottom-12 {
  padding-bottom: 12px;
}
.padding-bottom-30 {
  padding-bottom: 30px;
}
.margin-left-70 {
  margin-left: -70px;
}

@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}

@media (min-width: 1400px) {
  .contain-banner .container {
    width: 1366px;
  }
}